
import React ,{Component} from 'react'
// import Aux from '../../../hoc/Auxillary'
import classes from './OrderSummary.css'
import Button from '../../UI/Button/Button'

class OrderSummary extends Component{
    componentDidUpdate(){
        console.log("[order summary] will update")
    }


    render(){
        const ingredientSummary=Object.keys(this.props.ingredients)
        .map(igKey=>{
            return <li key={igKey} className={classes.li_items}><span style={{textTransform:'capitalize'}}>{igKey}: {this.props.ingredients[igKey]}</span></li>
        })

        return(
            <div className={classes.ordersummary}>
        <h3>Your Order</h3>
        <p className={classes.paragraph}>A delicious burger with the following ingredients:</p>
        <ul>
            {ingredientSummary}

        </ul>
        <p><strong>Total Price: {this.props.price.toFixed(2)}</strong></p>
        <p className={classes.checkout}>Continue to checkout?</p>
        <Button btnType="Danger" clicked={this.props.cancelClick}>CANCEL</Button>
        <Button btnType="Success" clicked={this.props.checkoutClick}>CHECKOUT</Button>
        </div>
        )
    }
   
   
    
}

export default OrderSummary;


